import { ref, watch } from 'vue'
import { defineStore } from 'pinia'
import api from '@/api/index'
import debounce from 'lodash.debounce'
import getFormConfigStore from './formConfig'
import { DEBOUNCE_DELAY } from '@/helpers/utils'

export const useInPdpInvoiceConfigStore = getFormConfigStore('inPdpInvoice')

const supplierFields = 'id,label'
const companyFields = 'id,name,active'
const supplierInvoiceFields = `id,official_number,date,status,supplier[${supplierFields}],company[${companyFields}]`
const defaultFields = `id,remote_number,recipient_number,date,reception_date,ht,tva,ttc,supplier_invoices[${supplierInvoiceFields}],status`
const defaultLoadOptions = {
    filters: {
        status: ['pending'],
        remote_number: '',
        company_ids: [],
        supplier_ids: [],
    },
    sort: { sortDirection: 'asc' },
    pagination: { page: 1, per_page: 50 },
    fields: defaultFields,
}
export const useInPdpInvoiceStore = defineStore('inPdpInvoice', () => {
    const loading = ref(true)
    const itemId = ref(null) // Pending Supplier Invoice Id we're working on
    const item = ref({}) // The current Pending Supplier Invoice object
    const itemCollection = ref([]) // The current Pending Supplier Invoice collection object
    const itemCollectionMeta = ref({}) // Meta data about the collection
    const itemLoadOptions = ref(defaultLoadOptions) // Load options for the current item

    const setInitialValues = async () => {
        await loadItemCollection()
        loading.value = false
    }
    const loadItemCollection = async (loadOptions = {}) => {
        const result = await api.inPdpInvoices.loadCollection(loadOptions)
        itemCollection.value = result.items
        itemCollectionMeta.value = result.metadata
    }
    const debounceApplyFilters = debounce(
        () => loadItemCollection(),
        DEBOUNCE_DELAY
    )
    watch(
        () => [
            itemLoadOptions.value.pagination.page,
            itemLoadOptions.value.pagination.per_page,
            itemLoadOptions.value.sort,
            itemLoadOptions.value.filters,
            itemLoadOptions.value.fields,
        ],
        async (newValues, oldValues) => {
            if (loading.value) {
                console.log('Still loading, not listening yet')
                return
            }
            if (newValues[0] == oldValues[0] && newValues[0] > 1) {
                // Si la page n'a pas changé mais qu'elle est supérieur à 1,
                // On met la page à 1
                // NB : On mutate la valeur donc le callback ici sera re-appellé
                // (et passera dans le else)
                // Pour éviter de charger deux fois les données
                // on ne charge pas cette fois-ci
                itemLoadOptions.value.pagination.page = 1
            } else {
                debounceApplyFilters()
            }
        },
        { deep: true }
    )
    return {
        loading,
        itemId,
        item,
        itemCollection,
        itemCollectionMeta,
        itemLoadOptions,
        setInitialValues,
        loadItemCollection,
    }
})
