<script setup>
    import { onMounted, ref } from 'vue'
    import {
        useInPdpInvoiceConfigStore,
        useInPdpInvoiceStore,
    } from '@/stores/in_pdp_invoice'
    import Table from '@/components/lists/Table.vue'
    import PaginationWidget from '@/components/PaginationWidget.vue'
    import FilterForm from './FilterForm.vue'
    import columnsDef from './columnsDef.js'
    import { collectOptions } from '@/helpers/context'
    const options = collectOptions()
    const store = useInPdpInvoiceStore()
    const configStore = useInPdpInvoiceConfigStore()
    configStore.setUrl(options.form_config_url)
    const loading = ref(true)
    // Destructure store properties
    const {
        itemCollection,
        itemCollectionMeta,
        itemLoadOptions,
        setInitialValues,
    } = store

    // Table configuration
    const tableParams = ref({
        columns: [
            'status',
            'remote_number',
            'reception_date',
            'company',
            'supplier',
            'ht',
            'tva',
            'ttc',
        ],
        columnsDef: columnsDef,
        sort: {
            sort: itemLoadOptions.sort?.sort || 'id',
            sortDirection: itemLoadOptions.sort?.sortDirection || 'desc',
        },
    })
    async function preload() {
        const promises = [setInitialValues(), configStore.loadConfig()]
        await Promise.all(promises)
        loading.value = false
    }
    // Methods
    const handleSort = (sortColumn, sortDirection) => {
        itemLoadOptions.sort = {
            sort: sortColumn,
            sortDirection: sortDirection,
        }
        tableParams.value.sort = {
            sort: sortColumn,
            sortDirection: sortDirection,
        }
    }

    // const handleFilter = (filters) => {
    //     // Update the store's filters
    //     Object.assign(itemLoadOptions.filters, filters)
    //     // Reset to first page when filtering
    //     itemLoadOptions.pagination.page = 1
    // }

    const handleAction = (action, item) => {
        switch (action) {
            case 'view':
                viewInvoice(item)
                break
            case 'open_supplier_invoice':
                if (item.supplier_invoice?.id) {
                    openSupplierInvoice(item.supplier_invoice.id)
                }
                break
            default:
                console.warn('Unknown action:', action)
        }
    }

    const viewInvoice = (invoice) => {
        // Implement view logic - could open a modal or navigate to detail page
        console.log('Viewing invoice:', invoice)
        // Example: window.openPopup(`/in_pdp_invoices/${invoice.id}?action=view`)
    }

    const openSupplierInvoice = (supplierInvoiceId) => {
        window.open(`/supplier_invoices/${supplierInvoiceId}`, '_blank')
    }
    await preload()
</script>

<template>
    <div v-if="loading" class="loading">
        <p>Chargement en cours...</p>
    </div>

    <div v-else id="app">
        <!-- Filters Section -->
        <FilterForm v-model:filters="itemLoadOptions.filters" />

        <!-- Pagination Top -->
        <PaginationWidget
            v-model:page="itemLoadOptions.pagination.page"
            v-model:itemsPerPage="itemLoadOptions.pagination.per_page"
            v-model:columns="tableParams.columns"
            :total-pages="itemCollectionMeta.total_pages || 1"
            :total-count="itemCollectionMeta.total_items || 0"
            :columns-def="tableParams.columnsDef"
        />

        <!-- Table -->
        <Table
            :items="itemCollection"
            :params="tableParams"
            @sort-by="handleSort"
        >
            <template #rowActions="{ item }">
                <td class="col_actions width_one">
                    <div class="btn-group">
                        <RouterLink
                            class="btn icon only"
                            :to="`/inpdpinvoice/${item.id}`"
                            title="Voir le détail de l'ordre de virement"
                            aria-label="Afficher le détail de l'ordre de virement"
                        >
                            <Icon name="eye" /> Voir
                        </RouterLink>
                    </div>
                </td>
            </template>
        </Table>

        <!-- Empty State -->
        <div v-if="itemCollection.length === 0" class="text-center py-4">
            <p class="text-muted">Aucune facture PDP trouvée.</p>
        </div>

        <!-- Pagination Bottom -->
        <PaginationWidget
            v-if="itemCollectionMeta.total_pages > 1"
            v-model:page="itemLoadOptions.pagination.page"
            v-model:itemsPerPage="itemLoadOptions.pagination.per_page"
            v-model:columns="tableParams.columns"
            :total-pages="itemCollectionMeta.total_pages || 1"
            :total-count="itemCollectionMeta.total_items || 0"
            :columns-def="tableParams.columnsDef"
        />
    </div>
</template>
